export default {
	data() {
		return {
			switchB: true
		};
	},
	onLoad() {
		this.getSettingMes()
	},
	methods: {
		// 查看已授权选项
		getSettingMes() {
			console.log('1111111')
			let _this = this;
			uni.getSetting({
				success(res) {
					console.log('123123213213', res.authSetting)
					if (res.authSetting['scope.userInfo']) {
						// 用户信息已授权，获取用户信息
						uni.getUserInfo({
							success(res) {
								console.log(res);
							},
							fail() {
								console.log("获取用户信息失败")
							}
						})
					} else if (!res.authSetting['scope.userInfo']) {
						console.log('失败了')
						// console.log("需要点击按钮手动授权")
						uni.authorize({
							scope: 'scope.userInfo',
							success(res) {
								console.log('授权成功')
								uni.getUserInfo({
									// 获取信息成功
									success(res) {
										console.log(res);
										// 成功后进行登录,获取code
										uni.login({
											success(res) {
												console.log(res);
												if (res.code) {
													//发起网络请求
													uni.request({
														// 请求路径
														url: 'https://test.com/onLogin',
														// 请求参数code
														data: {
															code: res.code
														},
														method: 'GET',
														success(res) {
															// 请求成功后获取openid和session_key
															console.log(res)
														}
													})
												} else {
													console.log('登录失败！' + res.errMsg)
												}
											}
										})
									},
									fail() {
										console.log("获取用户信息失败");
									}
								})
							},
							fail() {
								// 这里再次唤起
								uni.openSetting({
									success: (res) => {
										console.log('再次')
									}
								})
								console.log("授权失败");
							}
						})
					}
				},
				fail() {
					console.log("获取已授权选项失败")
				}
			})
		},
		changeSwitch(e) {
			this.switchB = e.detail.value
		}
	}
}
